import { createApp } from "vue";
import App from "./App.vue";
import store from "./store/";
import router from "./router";
import MenuIcon from "vue-material-design-icons/Menu.vue";

import VContainer from "./components/UI/VContainer.vue";
import VButton from "./components/UI/VButton.vue";
import VCard from "./components/UI/VCard.vue";
import VLabel from "./components/UI/VLabel.vue";
import VPage from "./components/layouts/ThePage.vue";

const app = createApp(App);

app.component("v-container", VContainer);
app.component("v-button", VButton);
app.component("v-card", VCard);
app.component("v-page", VPage);
app.component("v-label", VLabel);
app.component("menu-icon", MenuIcon);

app.use(store);
app.use(router);

app.mount("#app");
