<template>
   <nav class="navbar" :class="[shrinkNav, floating]">
      <v-container mode="flex align-center justify-between">
         <div class="nav-toggle">
            <v-button mode="icon" @click="toggleMenu" :class="hideNav">
               <span></span>
               <span></span>
               <span></span>
            </v-button>
         </div>
         <h3 class="nav-brand">Aldo Bangun</h3>
         <ul class="nav-actions">
            <li>
               <v-button mode="flat secondary">
                  Sign Up
               </v-button>
            </li>
            <li>
               <v-button mode="rounded primary">
                  Sign In
               </v-button>
            </li>
         </ul>
      </v-container>
      <TheSidebar :isActive="menuIsVisible" />
   </nav>
</template>

<script>
import TheSidebar from "./TheSidebar";

export default {
   components: { TheSidebar },
   data() {
      return {
         menuIsVisible: false,
         isScrolled: false,
      };
   },
   methods: {
      toggleMenu() {
         this.menuIsVisible = !this.menuIsVisible;
      },
   },
   computed: {
      hideNav() {
         return { close: this.menuIsVisible };
      },
      shrinkNav() {
         return { "side-active": this.menuIsVisible };
      },
      floating() {
         return { floating: this.isScrolled };
      },
   },
   created() {
      window.addEventListener("scroll", () => {
         const { scrollY } = window;

         scrollY > 0 ? (this.isScrolled = true) : (this.isScrolled = false);
      });
   },
};
</script>

<style scoped>
.navbar {
   background-color: var(--light);
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   height: 4.5rem;
   width: 100vw;
   margin-left: auto;
   transition: 0.15s linear;
   z-index: 99;
}

.navbar.floating {
   box-shadow: 0 1px 5px rgba(0, 0, 0, 0.16);
}

.side-active {
   width: calc(100vw - 15rem);
}

.nav-actions {
   display: flex;
   align-items: center;
   gap: 0.5rem;
}

.nav-toggle span {
   display: inline-block;
   width: 1.5rem;
   height: 2px;
   background-color: var(--dark);
   transition: 0.15s ease-out;
}

.nav-toggle span:first-of-type {
   transform: translateX(-0.5rem);
}

.nav-toggle span:last-of-type {
   transform: translateX(0.5rem);
}

.nav-toggle:hover span,
.nav-toggle .close span {
   transform: translateX(0);
}
