<template>
   <ul class="blogs">
      <BlogItem
         v-for="blog in blogs"
         :key="blog.id"
         :blogId="blog.id"
         :title="blog.title"
         :author="blog.author"
         :date="blog.date"
      />
   </ul>
</template>

<script>
import BlogItem from "./BlogItem";
import { mapGetters } from "vuex";

export default {
   components: {
      BlogItem,
   },
   computed: {
      ...mapGetters({
         blogs: "blogs/blogs",
      }),
   },
};
</script>

<style scoped>
.blogs {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 1rem;
   color: var(--dark);
}
</style>
