<template>
   <v-card mode="darken">
      <h3>{{ title }}</h3>
      <small class="date">{{ date }}</small>
      <a :href="link"> {{ author }}</a>
      <div class="actions">
         <v-button mode="outline">
            Check it out
         </v-button>
      </div>
   </v-card>
</template>

<script>
export default {
   props: ["blogId", "title", "author", "date"],
   computed: {
      link() {
         return (
            "#/user/" +
            this.author
               .toLowerCase()
               .split(" ")
               .join("-")
         );
      },
   },
};
</script>

<style scoped>
.date {
   display: block;
}

.actions {
   margin: 2rem 0 0;
}
</style>
