<template>
   <div>
      <header><h1>Sign In</h1></header>
      <form @submit.prevent="submitForm">
         <v-label>
            <label for="username">Username</label>
            <input type="text" id="username" />
         </v-label>
         <div class="actions">
            <v-button mode="flat secondary" :to="switchMode">Sign Up</v-button>
            <v-button mode="rounded primary">Sign In</v-button>
         </div>
      </form>
   </div>
</template>

<script>
export default {
   methods: {
      submitForm() {
         console.log("proses login");
      },
   },
   computed: {
